<template>
  <div class="homeSwiperList">
    <swiper :options="swiperOption">
      <swiper-slide v-for="slide in swiperSlides">
        <homeProduct></homeProduct>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
import homeProduct from '~/components/homeProduct.vue'

  export default {
    data() {
      return {
        swiperOption: {
          loop: true,
          pagination: '.swiper-pagination',
          slidesPerView: 2,
          centeredSlides: true,
          paginationClickable: true,
          spaceBetween: 30,
          grabCursor: true
        },
        swiperSlides: [1, 2, 3, 4, 5]
      }
    },
    mounted() {

    },
    components: {
      homeProduct
    }
  }
</script>

<style lang="scss" scoped>
.swiper-container{
}
.swiper-slide{
  background: #fff;
}

</style>



