<template>
<div class="homePageTop" :style="{ background: background,color:color}">
  {{ company }}
</div>
</template>

<script>
export default {
  data() {
    return {}
  },
  props: {
    company: {
      type: String,
      default: '精选活动'
    },
    background: {
      type: String,
      default: 'rgba(0,0,0,0)'
    },
    color: {
      type: String,
      default: '#000'
    }
  }
}
</script>

<style lang="scss" scoped>
@media (max-width: 575px) {
  .homePageTop {
      font-size: 18px;
      padding: 15px;
      font-weight:200;
  }
}

@media (min-width: 576px) {
  .homePageTop {
      font-size: 18px;
      padding-top: 20px;
      padding-bottom: 0;
      font-weight:200;
  }
}

</style>



