<template>
  <div class="homeSwiper">
    <swiper :options="swiperOption">
      <swiper-slide v-for="slide in swiperSlides">I'm Slide {{ slide }}</swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
  export default {
    name: 'carrousel',
    data() {
      return {
        swiperOption: {
          autoplay: 3500,
          setWrapperSize :true,
          pagination : '.swiper-pagination',
          paginationClickable :true,
          mousewheelControl : true,
          observeParents:true,
          loop: true
        },
        swiperSlides: [1, 2, 3]
      }
    },
    mounted() {

    }
  }
</script>

<style lang="scss" scoped>
@media (max-width: 575px){
  .homeSwiper{
    height:350px;
    background: #1D8CE0;
    .swiper-container{
      height:100%;
    }
  }
}

@media (min-width: 576px){
  .homeSwiper{
    height:500px;
    background: #1D8CE0;
    .swiper-container{
      height:100%;
    }
  }
}



</style>



