<template>
<div class="homePaper">
  <div class="homePaper-box">
    <div class="paper-left">
      <span>￥</span><span>30000</span>
    </div>
    <div class="paper-middle">
      <p>企业发布系统</p>
      <p>买卖商品的前提</p>
    </div>
    <div class="paper-right">
      <button>拨打电话</button>
    </div>
  </div>
</div>
</template>

<script>
export default {
  data() {
    return {}
  }
}
</script>

<style lang="scss" scoped>
@media (max-width: 575px) {
    .homePaper {
        padding: 10px;
        .paper-left {
            width: 30%;
            color: #C79022;
            span:nth-child(2) {
                font-size: 25px;
            }
        }
        .paper-middle {
            width: 40%;
            font-size: 13px;
            font-weight: bold;
            text-align: center;
        }
        .paper-middle p:nth-child(2) {
          font-size: 15px;
          font-weight: 100;
