            flex-wrap: wrap;
            background: #EFF2F7;
            border-radius: 2px;
            padding: 15px;
        }
    }
}
@media (min-width: 576px) {
  .homePaper {
      padding: 15px 0;
      max-width: 1200px;
      margin: 0 auto;
      .paper-left {
          width: 30%;
          color: #C79022;
          text-align: center;
          span:nth-child(2) {
              font-size: 25px;
          }
      }
      .paper-middle {
          width: 40%;
          font-size: 15px;
          font-weight: bold;
      }
      .paper-middle p:nth-child(2) {
        font-size: 17px;
        font-weight: 100;
      }
      .paper-right {
          width: 30%;
          background-color: #1D8CE0;
          border-radius: 3px;
          button{
            text-align: center;
            width:100%;
            height:100%;
            color:#fff;
            background-color: rgba(0,0,0,0);
