<template>
  <div class="homeSwiperListOne">
    <swiper :options="swiperOption">
      <swiper-slide v-for="slide in swiperSlides">
        <homeList></homeList>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
import homeList from '~/components/homeList.vue'

  export default {
    data() {
      return {
        swiperOption: {
          loop: true,
          pagination: '.swiper-pagination',
          slidesPerView: 4,
          centeredSlides: true,
          paginationClickable: true,
          spaceBetween: 35,
          grabCursor: true
        },
        swiperSlides: [1, 2, 3, 4, 5]
      }
    },
    mounted() {

    },
    components: {
      homeList
    }
  }
</script>

<style lang="scss" scoped>
.swiper-container{
}
.swiper-slide{
  background: #fff;
}

</style>



